<template>
  <div>
    <h1>debounce</h1>
    <p>
       JavaScript debounce
    </p>
    <v-btn @click="msg = '', count = 0">
      
    </v-btn>
    <v-btn @click="handleClick">
      
    </v-btn>
    <v-btn @dblclick="handleClick">
       + 
    </v-btn>
    <v-btn @click="handleClick2">
       {{ delay }}ms
    </v-btn>
    <v-btn @dblclick="handleClick2">
       {{ delay }}ms + 
    </v-btn>
    <v-text-field
      v-model="delay"
      dense
      outlined
      type="number"
      label="ms"
      style="display: inline-block;width: max-content;"
    />
    <hr class="my-2">
    <p>_ <a href="https://github.com/lodash/lodash/blob/4.17.15/lodash.js#L10304">lodash</a></p>
    <p> <a href="https://css-tricks.com/debouncing-throttling-explained-examples">https://css-tricks.com/debouncing-throttling-explained-examples</a> </p>
    <v-btn @click="msg = '', count = 0">
      
    </v-btn>
    <v-btn @click="handleClickH">
      _ 
    </v-btn>
    <v-btn @dblclick="handleClickH">
      _  + dbl
    </v-btn>
    <v-btn @click="cancelDebounce">
      
    </v-btn>
    <v-btn @click="execDebounce">
      
    </v-btn>
    <hr class="my-2">
    <div>
      {{ count }}<br>
      {{ msg }}
    </div>
  </div>
</template>

<script>
import _ from "lodash";

let that = null;
const handleClickH = _.debounce(function () {
  console.log("");
  that.msg += " ";
  that.count++;
}, 500);
const cancelDebounce = () => {
  console.log("");
  handleClickH.cancel();
};
const execDebounce = () => {
  console.log("");
  handleClickH.flush();
};

let timer = null;
export default {
  data() {
    return {
      count: 0,
      msg: "",
      delay: 200,
      handleClickH,
      cancelDebounce,
      execDebounce,
    };
  },
  mounted() {
    that = this;
  },
  methods: {
    myDebounce(fn, delay = 0) {
      console.log('', fn, delay);
      return ( ...args ) => {

        if (timer) {
          clearTimeout(timer);
        }

        timer = setTimeout(() => {
          fn.apply(this, args);
        }, delay);
      };
    },
    handleClick() {
      this.msg += " ";
      this.count++;
    },
    handleClick2() {
      this.myDebounce(() => {
        this.msg += " ";
        this.count++;
      }, this.delay)();
    },
    // // debounce
    // handleClickH: _.debounce(function () {
    //   this.msg += " ";
    //   this.count++;
    // }, 500),
    // cancelDebounce() {
    //   // 
    //   this.handleClickH.cancel();
    // },
    // execDebounce() {
    //   // 
    //   this.handleClickH.flush();
    // },
  },
};
</script>

